export default {
	data() {
		return {
			activeColor: true,
			width: '',
			height:'',
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			markers: [
				{
					latitude: 39.9085,
					longitude: 116.39747,
					iconPath: '../../static/location@3x.png'
				}
			]
		};
	},
	onReady() {
		var _self = this;
		uni.getSystemInfo({
			success: function(res) {
				_self.width = res.windowWidth;
				_self.height = res.windowHeight;
				
			}
		});
	},
	methods: {}
};
